$pc:1920px;
$labtop:1024px;
$mobile:640px;

.popular_slider_cover,.television_slider_cover{
    padding: 3em;
    transition: 1s ease-in-out;
    h1{
        font-size: 80px;
        margin-bottom: 1em;
        text-align: center;
    }
    .popular_slider,.television_slider{
        
        .card,.tv_card{
            position: relative;
            z-index: 1;
            color: rgb(255, 255, 255);
            width: 20vw;
            transition: 1.5s ease-in-out;
            &:hover{
                z-index: 1;
                transform: scale(1.2) ;
                
                } 
            &:hover p{
                    transform:translateY(-3em);
                    visibility: hidden;
            }
                p{
                    font-size: 2em;
                    font-weight: bold;
                    text-align: center;
                    transition: .5s ease-in-out;
                    min-height: 4em;
                    margin-bottom: 1em;
                }
                img{
                    max-width: 100%;
                    max-height: 100%;
                    border-radius: 30px;
                    border: 1em transparent;
                }
            }
        .tv_card{
            width: 15vw;
        }
    }
}
.video_slider{
    border-top: 3px solid white;
    background-color: black;
    color: aliceblue;
    padding: 0 3em;
    h1{
        margin: 1em 0px;
        font-size: 2em;
    }
    .YouTube{
        max-height: 80%;
        max-width: 100%;
    }
}
@media screen and (min-width:$labtop) and (max-width:$pc){
    .popular_slider_cover,.television_slider_cover{
        .popular_slider,.television_slider{
        .card{
            width: 20vw;
            
            }
        .tv_card{
            width: 15vw;
            
        }
        }
    }
}

@media (min-width:$mobile) and (max-width:$labtop){
    .popular_slider_cover,.television_slider_cover{
        .popular_slider,.television_slider{
        .card{
            width: 25vw;
            }
        .tv_card{
            width: 27vw;
        }
        }
    }
    .video_slider{
        padding: 0 2em;
        h1{
            margin: .5em 0 ;
            font-size: 2em;
        }
        .YouTube{
            max-height: 60%;
            max-width: 100%;
        }
    }
    
}
@media (max-width:$mobile){
    .popular_slider_cover,.television_slider_cover{
        h1{margin: 0;}
        .popular_slider,.television_slider{
        .card,.tv_card{
            width: 85vw;
            }
        }
    }
}